.p-curriculum {
  &__image {
    background-color: #EEE;
    margin-bottom: 133px;

    img {
      display: block;
      max-width: 1327px;
      margin: 0 auto;
      width: 100%;

      &.curriculum2 {
        max-width: 752px;
      }
    }

    @include max-screen(768px) {
      margin-bottom: 63px;
      overflow-x: scroll;

      img {
        max-width: none;
        width: 1200px;

        &.curriculum2 {
          width: 752px;
        }
      }
    }
  }
}
