.l-wrapper {
  overflow: hidden;
  margin: 0 auto;
  min-width: $content_width;

  @include max-screen(768px) {
    min-width: 0;
  }
}

.l-main {
  padding-top: 56px;
}

.l-container {
  padding: 0 $content_padding;
  margin: 0 auto;
  width: $content_width;

  @include max-screen(768px) {
    padding: 0 28px;
    width: 100%;
  }
}
