.l-footer {
  margin: 0 auto;
  margin-top: -56px;
  max-width: $content_width;
  padding-bottom: 14px;


  @include max-screen(768px) {
    margin-top: 21px;

    @at-root {
      .index & {
        margin-top: 0;
      }
    }
  }

  &__pagetop {
    height: 28px;
    margin-bottom: 70px;
    margin-left: auto;
    margin-right: 0;
    width: 28px;

    img {
      display: block;
    }

    @include max-screen(768px) {
      margin-bottom: 28px;
      margin-right: 28px;
    }
  }

  &__nav {
    font-size: 18px;
    font-weight: bold;
    // line-height: 42px;
    margin-bottom: 105px;
    padding: 7px 0;
    text-align: right;

    @include max-screen(768px) {
      font-size: 14px;
      margin-bottom: 49px;
    }

    a {
      background: url('../images/common/arrow_right.svg') no-repeat right bottom 12px;
      background-size: 7px 12px;
      color: $green;
      display: inline-block;
      padding-right: 14px;
      @include transition;

      &:hover {
        transform: translateX(7px);
      }

      @include max-screen(768px) {
        background-position: right bottom 8px;

        &:hover {
          transform: none;
        }
      }
    }

    span {
      display: inline-block;
    }
  }

  &__copyright {
    font-size: 10px;
    line-height: 28px;
  }

}
