.c-section {
  margin-bottom: 28px;

  @include max-screen(768px) {
    margin-bottom: 56px;
  }

  &__inner {
    // align-items: flex-start;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

    @include max-screen(768px) {
      display: block;
    }
  }

  &__heading {
    font-family: $serif;
    font-size: 28px;
    line-height: 49px;
    margin-top: -10.5px;
    width: 392px;

    @include max-screen(768px) {
      width: 100%;
    }

    span {
      display: inline-block;
    }
  }

  &__content {
    margin-bottom: 7px;
    margin-top: -7px;
    width: 490px;

    @include max-screen(768px) {
      margin-top: 14px;
      text-align: justify;
      width: 100%;
    }

    p {
      margin-bottom: 28px;
    }

    h4 {
      font-weight: bold;
    }
  }
}
