.p-access {
  @include max-screen(768px) {
    margin-bottom: 0;
  }

  &__address {
  }

  &__map {
    margin-left: -140px;
    width: 630px;

    img {
      display: block;
      width: 100%;
    }

    @include max-screen(768px) {
      margin-left: 0;
      width: 100%;
    }

    &__link {
      background: url('../images/common/arrow_right.svg') no-repeat right center;
      background-size: 5px 8px;
      color: $green;
      display: inline-block;
      padding-right: 10px;
      @include transition;

      &:hover {
        color: $green;
        transform: translateX(4px);
      }

      @include max-screen(768px) {
        &:hover {
          transform: none;
        }
      }
    }
  }
}
