.p-index {
  &__logo {
    height: 255px;
    margin-left: auto;
    margin-right: 56px;
    margin-top: -235px;
    position: relative;
    width: 378px;
    z-index: 1;

    img {
      display: block;
    }

    @include max-screen(768px) {
      height: 145px;
      margin-right: -4px;
      margin-top: -51px;
      width: 215px;
    }
  }

  &__mainimage {
    margin-top: -8px;

    img {
      display: block;
    }

    @include max-screen(768px) {
      margin-left: -28px;
      margin-top: -39px;
      width: 100vw;
    }
  }

  &__maintext {
    font-family: $serif;
    font-size: 28px;
    height: 308px;
    margin-left: auto;
    margin-right: -14px;
    margin-bottom: 84px;
    margin-top: 70px;
    width: 100%;
    writing-mode: vertical-rl;

    @include max-screen(768px) {
      font-size: 21px;
      height: 231px;
      margin-bottom: 105px;
      margin-right: -10.5px;
      margin-top: 42px;
    }
  }

  &__about {
    // @include img2x('../images/index/about', 'png', contain, bottom);
    background: url(../images/index/about@2x.png) no-repeat bottom;
    background-size: 100% auto;
    margin: 0 auto;
    max-width: 1420px;
    padding-bottom: (750 / 1420 * 100%);

    @include min-screen(1420px) {
      padding-bottom: 750px;
    }

    @include screen(769px, $content_width) {
      padding-bottom: (750 / 1420 * $content_width);
    }

    @include max-screen(768px) {
      background-size: 140vw auto !important;
      background-position: left -25vw bottom !important;
      margin-bottom: 0;
      padding-bottom: 70vw;
    }
  }
}
