.l-header {
  padding: 28px 0;

  &__title {
    img {
      display: block;
    }

    @include max-screen(768px) {
      margin-left: auto;
      margin-right: 0;
      width: 241px;
    }

    .title1 {
      height: 86px;
      width: 318px;
      margin-bottom: 42px;

      @include max-screen(768px) {
        height: 56px;
        width: 209px;
        margin-bottom: 21px;
      }
    }

    .title2 {
      height: 109px;
      width: 367px;

      @include max-screen(768px) {
        height: 72px;
        width: 241px;
      }
    }
  }

  &__nav {
    position: fixed;
    top: 28px;
    z-index: 10;
    width: 100%;

    &__button {
      $this: &;
      background: $gray;
      color: transparent;
      cursor: pointer;
      height: 42px;
      left: 0;
      overflow: hidden;
      position: absolute;
      top: 0;
      width: 42px;
      z-index: 11;

      span {
        background: #FFF;
        display: block;
        height: 2px;
        left: 11px;
        position: absolute;
        @include transition;
        width: 19px;

        &:nth-child(1) {
          top: 13px;
        }
        &:nth-child(2) {
          top: 20px;
        }
        &:nth-child(3) {
          top: 27px;
        }
      }

      &:hover {
        span {
          &:nth-child(1) {
            transform: translateY(-2px);
          }
          &:nth-child(3) {
            transform: translateY(2px);
          }
        }
      }

      &.is-open {
        span {
          &:nth-child(1) {
            transform: translateY(7px) rotate(45deg);
          }
          &:nth-child(2) {
            opacity: 0;
          }
          &:nth-child(3) {
            transform: translateY(-7px) rotate(-45deg);
          }
        }
      }
    }

    &__list {
      display: none;
      background: $gray;
      height: 404px;
      padding: 60px 42px;
      width: 392px;

      @include max-screen(768px) {
        height: auto;
        padding: 60px 42px 42px;
        max-width: 392px;
        width: calc(100% - 28px);
      }

      a {
        color: #FFF;
        display: block;
        font-size: 21px;
        font-family: $serif;
        margin-bottom: 21px;
        @include transition;

        &:hover {
          transform: translateX(7px);
        }

        @include max-screen(768px) {
          font-size: 20px;
        }
      }

      span {
        display: inline-block;
      }
    }
  }

}
