.p-research {
  @include max-screen(768px) {
    margin-bottom: 0;
  }

  &__heading {
    @include img2x('../images/research/research_bg', 'jpg', 267px 1766px, right 14px top 196px);

    @include max-screen(768px) {
      background: none;
    }
  }

  &__prof {
    margin-bottom: 14px;

    &:not(:first-child) {
      margin-top: 63px;
    }

    &:after {
      background: rgba(34,24,21,.4);
      content: " ";
      display: block;
      height: 1px;
      width: 28px;
    }

    p {
      margin-bottom: 14px;
    }

    &__image {
      height: 210px;
      margin-left: auto;
      margin-right: 0;
      width: 210px;

      img {
        display: block;
      }

      @include max-screen(768px) {
        height: 140px;
        margin-top: 49px;
        width: 140px;
      }
    }

    &__name {
      margin-bottom: 28px;
      margin-top: -21px;

      @include max-screen(768px) {
        // padding-right: 140px;

        &.line2 {
          margin-top: -49px;

          strong {
            display: block;
          }
        }
      }

      strong {
        display: inline-block;
        font-size: 18px;
        font-weight: bold;
        line-height: 28px;
        margin-right: .3em;
      }
    }

    &.small {
      position: relative;

      .p-research__prof__image {
        height: 94px;
        margin-top: 7px;
        position: absolute;
        right: 0;
        top: 0;
        width: 94px;
      }

      .p-research__prof__name {
        margin-top: 0;
      }

      > p {
        padding-right: 119px;
      }
    }
  }
}
